
import { defineComponent } from "vue";
import { uiDescriptions } from "../ui-descriptions";
import { mapMutations, mapState } from "vuex";
import PopUpMenu from "@/components/PopUpMenu.vue";
import CivilizationItem from "./CivilizationItem.vue";
import { game } from "@/model/game";
import { resourceDataDict } from "@/content/resource-data";
import { ResourceData } from "@/model/data-interfaces";
import {
  getPopulationStatus,
  getPopulationStatusString,
} from "@/content/population-statuses";
import { formatNumber, formatDown } from "../format";
import ColoredResource from "@/components/ColoredResource.vue";

export default defineComponent({
  name: "EmpireDisplay",
  props: [
    "civilizations",
    "empireRates",
    "name",
    "population",
    "maxPotentialRates",
    "empireMult",
    "prestigeUnlocked",
  ],
  data() {
    return {
      uiDescriptions: uiDescriptions,
      newName: "",
    };
  },
  emits: ["prestige"],
  components: {
    PopUpMenu,
    CivilizationItem,
    ColoredResource,
  },
  computed: {
    ...mapState(["settings"]),
  },
  methods: {
    ...mapMutations([
      "togglePopupOpen",
      "hoverDescString",
      "hoverDescStringReg",
      "resetDesc",
    ]),
    getResData(resId: number): ResourceData {
      return resourceDataDict[resId];
    },
    formatNumber(num: number) {
      return formatNumber(num, undefined);
    },
    getPopString(pop: number): string {
      return getPopulationStatusString(getPopulationStatus(pop));
    },
    formatPop(num: number) {
      return formatDown(num);
    },
    setName() {
      game.name = this.newName;
    },
  },
});
